
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.border-left {
  position: relative;
}
.border-left:before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 1px;
  background-color: #dedede;
  -webkit-transform-origin: 50% 0;
  transform-origin: 50% 0;
  transform: scaleX(0.5);
  -webkit-transform: scaleX(0.5);
  z-index: 15;
}
.border-right {
  position: relative;
}
.border-right:after {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 1px;
  background-color: #dedede;
  -webkit-transform-origin: 50% 0;
  transform-origin: 50% 0;
  transform: scaleX(0.5);
  -webkit-transform: scaleX(0.5);
  z-index: 15;
}
.border-top {
  position: relative;
}
.border-top:before {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  height: 1px;
  background-color: #dedede;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
  z-index: 15;
}
.border-bottom {
  position: relative;
}
.border-bottom:after {
  content: " ";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 1px;
  background-color: #dedede;
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
  -webkit-transform: scaleY(0.5);
  z-index: 15;
}
.ratio-1x .border-left:after,
.ratio-1x .border-right:after {
  -webkit-transform: scaleX(1);
  transform: scaleY(1);
}
.ratio-1x .border-top:after,
.ratio-1x .border-bottom:after {
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
}
.ratio-2x .border-left:after,
.ratio-2x .border-right:after {
  -webkit-transform: scaleX(0.5);
  transform: scaleY(0.5);
}
.ratio-2x .border-top:after,
.ratio-2x .border-bottom:after {
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.ratio-3x .border-left:after,
.ratio-3x .border-right:after {
  -webkit-transform: scaleX(0.3);
  transform: scaleY(0.3);
}
.ratio-3x .border-top:after,
.ratio-3x .border-bottom:after {
  -webkit-transform: scaleY(0.3);
  transform: scaleY(0.3);
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.right {
  text-align: right !important;
}
.left {
  text-align: left !important;
}
.center {
  text-align: center !important;
}
.vcenter {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: auto;
  text-align: center;
  -webkit-transform: translate(0, -50%);
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  -o-transform: translate(0, -50%);
}
.block {
  display: block;
}
.hidden {
  display: none !important;
}
.invisible {
  visibility: hidden !important;
}
.text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.content {
  position: relative;
  margin-bottom: 0.67rem;
  padding-bottom: .15rem;
}
.padding-wrap {
  padding: 0 .15rem;
}
.btn {
  width: 100%;
  height: .32rem;
  color: #fff;
  background-color: #e67e65;
  line-height: .32rem;
  text-align: center;
  border-radius: 5px;
  background-clip: padding-box;
  cursor: pointer;
}
.btn:active {
  background-color: #df5939;
}
.fund-invest {
  position: relative;
  width: 100%;
  height: .45rem;
  background-color: #3668a3;
  color: #fff;
  line-height: .45rem;
}
.fund-invest .fund {
  float: left;
  font-size: .14rem;
}
.fund-invest .invest {
  float: right;
  font-size: .12rem;
  width: .5rem;
}
.fund-invest .invest .btn {
  background-color: #d65452;
  margin-top: .06rem;
}
.fund-invest .invest .btn:active {
  background-color: #c6312f;
}
.header {
  width: 100%;
  position: relative;
  text-align: center;
  color: #fff;
}
.header .first-title {
  height: 0.4rem;
  background-color: #262626;
  line-height: 0.4rem;
  font-size: .16rem;
}
.header .sub-title {
  height: 0.5rem;
  background-color: #2e2e2e;
  line-height: 0.5rem;
  font-size: .18rem;
}
.footer {
  width: 100%;
  height: 0.67rem;
  position: fixed;
  bottom: 0;
  font-size: .14rem;
  background-color: #333333;
}
.footer a {
  display: block;
  width: 100%;
  height: 100%;
}
.footer .nav-tabs {
  width: 100%;
  height: 100%;
  display: table;
}
.footer .nav-tabs a {
  background-color: #212e37;
  text-align: center;
  line-height: 0.67rem;
  color: #d9d9d9;
}
.footer .nav-tabs li {
  display: table-cell;
  height: 100%;
}
.footer .nav-tabs li:hover a,
.footer .nav-tabs li.active a {
  background-color: #0e1317;
  color: #fff;
}
.btn-wrap {
  display: table;
  width: 100%;
  margin-top: .18rem;
  padding-left: .15rem;
}
.btn-wrap li {
  display: table-cell;
  padding-right: .15rem;
}
.product {
  width: 100%;
  color: #fff;
  text-align: left;
  border-bottom: 0.01rem solid #333333;
}
.product th {
  background-color: #333333;
  font-size: .12rem;
  height: .3rem;
  color: #808080;
}
.product td {
  font-size: .14rem;
  height: .5rem;
}
.product td .update {
  color: #737373;
  font-size: .12rem;
}
.product td.data {
  color: #14B143;
}
.k-line {
  width: 100%;
  height: 3rem;
}
.form {
  padding: .3rem .15rem;
}
.form .input-wrap {
  width: 100%;
  height: .35rem;
  line-height: .35rem;
  color: #cccccc;
  font-size: .14rem;
  margin-bottom: .12rem;
  position: relative;
}
.form .input-wrap .text {
  float: left;
  width: 26%;
  text-align: right;
  padding-right: .1rem;
  height: .35rem;
  line-height: .35rem;
}
.form .input-wrap input,
.form .input-wrap select {
  float: left;
  width: 52%;
  height: .35rem;
  line-height: .35rem;
  background-color: #333333;
  padding: 0 .15rem;
  color: #bfbfbf;
}
.form .input-wrap .tips {
  float: left;
}
.form .input-wrap input[type=checkbox] {
  position: absolute;
  top: 0;
  left: 4%;
  width: .14rem;
}
.buy-form .tips {
  width: 100%;
  text-align: center;
}
.buy-form .tips span {
  margin-right: .2rem;
}
.sell-form .tips {
  color: #9d4b62;
  width: 20%;
  padding-left: .1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
@media screen and (min-width: 1025px) {
.container {
    width: 700px;
    margin-left: auto;
    margin-right: auto;
}
.footer {
    width: 700px;
}
}



